import React, { useState, useEffect } from 'react';
import axios from "axios";
import './login.css';
import roots_logo from './../../img/roots_logo_transparent_256.png';

/*
OVERALL FILE PURPOSE: The Login/Register Page that users are directed to when they're not yet logged in.
Contains all the logic to register for another account or login.
*/

const Login = (props) => {
    const [error, setError] = useState(false);
    const [username, setUsername] = useState('');
    const [password, set_password] = useState('');
    const [firstName, set_firstName] = useState('');
    const [lastName, set_lastName] = useState('');
    const [form, set_form] = useState('login')

    /*
    Input from: None
    Outputs to: Login.js/return()
    Created by: Kelvin F 08/19/2022
    Last Edit: Kelvin F 08/19/2022
    Purpose: Treats keyboard enter as equivalent to clicking login button
    */
    function handleKeyPress(event) {
        if(event.key === 'Enter'){
          login()
        }
    }

    /*
    Input from: None
    Outputs to: Login.js/return()
    Created by: Kelvin F 08/19/2022
    Last Edit: Kelvin F 08/19/2022
    Purpose: Sends API call to backend to verify user login credentials
    */
    function login(){
      const payload = { username: username, password: password } 

      if (form) {
        payload.first_name = firstName
        payload.last_name = lastName
      }
      axios
        .post(`/auth/${form}/`, payload)
        .then(res => {
          console.log("LOGIN DATA: ", res)
          const token = "Token "+res.data['token'];   
          // We set the returned token as the default authorization header
          localStorage.setItem('token', token)
          localStorage.setItem('user', username)
          axios.defaults.headers.common.Authorization = token;
          console.log("LOGIN SET: ", axios.defaults.headers.common.Authorization)
          window.location.replace("/roots/#/experiments")

        })
        .catch(error => {
          if(error.response.status === 409){
            alert("Username already taken.")
          }
          setError(true)
          console.log(error)
        });
    }


    /*
    Input from: None
    Outputs to: Login.js/return()
    Created by: Kelvin F 08/19/2022
    Last Edit: Kelvin F 08/19/2022
    Purpose: Returns Login page to be rendered 
    */
    return (
        <div className="login">
          <img src={roots_logo} style={{minWidth: '50px', width:'5%', marginBottom:'5%'}} alt=""/>
          <div className="login-form">
            <input placeholder="kfilyk" autoCorrect={false} autoCapitalize="none" onChange={e => setUsername(e.target.value)} onKeyPress={(key) => handleKeyPress(key)}/>
            <input secureTextEntry autoCapitalize="none" autoCorrect={false} placeholder="rootspass123" onChange={e => set_password(e.target.value)} onKeyPress={(key) => handleKeyPress(key)} />
            {form === "register" && <input placeholder="firstName" autoCorrect={false} autoCapitalize="none" onChange={e => set_firstName(e.target.value)} onKeyPress={(key) => handleKeyPress(key)}/>}
            {form === "register" && <input placeholder="lastName" autoCorrect={false} autoCapitalize="none" onChange={e => set_lastName(e.target.value)} onKeyPress={(key) => handleKeyPress(key)}/>}
          </div>
          <br/>
          <button className="sign-up-button" onClick={() => {set_form(form === "login" ? "register" : "login"); setError(false)}}>{form==="login" ? "Sign Up" : "Sign In"}</button>
          <button className="login-button" onClick={() => login()}></button>
          {error ? "Credentials invalid.": ""}
        </div>
      );
}
  

export default Login